.btnBar {
  margin: auto;
  border-radius: 25px;
  display: inline-block;
}

.emojiBar {
  margin: 50px;
  text-align: center; 
}

.shadowBox {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  text-align: center;
}

.center {
    width: 100px;
    height: 100px;
    background-color: red;

    position: absolute;
    top:0;
    bottom: 0;
    left: 0;
    right: 0;

    margin: auto;
}

td th {
  width: 100px;
}

.btnDrop {
  width: 350px;
}

#scrollable-menu {
  height: auto;
  max-height: 170px;
  overflow-x: hidden;
}
/* Table */

.container-table {
  display: table;
}

.container-table-row {
  display: table-row;
}

.container-table-cell {
  display: table-cell;
}
